






















import Vue from 'vue';
import router from '@/router';
import Cookie from 'js-cookie';

export default Vue.extend({
  name: 'recipe',
  props: {
    recipe: {
      type: Object,
      required: true
    },
    volume: {
      type: Number,
      required: true
    }
  },
  beforeMount(): void {
    // if the user is not logged in send them to the login page
    if (!Cookie.getJSON('loggedIn')) {
      router.push('/');
    }
  }
});
