



















import Vue from 'vue';
import Cookie from 'js-cookie';
import router from '../router';

interface INavbarState {
  admin: false;
}

export default Vue.extend({
  name: 'navbar',

  data(): INavbarState {
    return {
      admin: false
    };
  },

  props: ['activeState'],

  beforeMount() {
    this.admin = Cookie.getJSON('loggedIn').admin;
  },

  methods: {
    home() {
      if (!Cookie.getJSON('loggedIn')) {
        router.push('/');
      } else {
        router.push('/home');
      }
    },
    batchHistory() {
      if (!Cookie.getJSON('loggedIn')) {
        router.push('/');
      } else {
        router.push('/batch-history');
      }
    },
    tankHistory() {
      if (!Cookie.getJSON('loggedIn')) {
        router.push('/');
      } else {
        router.push('/tank-history');
      }
    },
    adminPage() {
      if (!Cookie.getJSON('loggedIn')) {
        router.push('/');
      } else {
        router.push('/admin');
      }
    }
  }
});
