























import Vue from 'vue';
import Cookie from 'js-cookie';
import { Tank, BrewhopsCookie } from '@/types/index';

interface ICreateTankState {
  tank_name: string;
  status: string;
  in_use: boolean;
  feedback: {
    tank_name: string;
    server: {
      tank: string;
    };
  };
}

// tslint:disable: no-console

export default Vue.extend({
  name: 'create-tank',
  props: ['tanks', 'statuses'],
  data(): ICreateTankState {
    return {
      tank_name: '',
      status: '',
      in_use: false,
      feedback: {
        tank_name: '',
        server: {
          tank: ''
        }
      }
    };
  },
  methods: {
    async tank_submit() {
      const tankExists = this.tanks.filter((tank: Tank) => tank.name === this.tank_name);

      if (tankExists.length === 0) {
        const tank: Tank = {
          name: this.tank_name,
          status: this.status,
          in_use: this.in_use
        };

        const headers = {
          Authorization: `Bearer ${Cookie.getJSON('loggedIn').token}`
        };

        try {
          const response = await this.$http.post(`${process.env.VUE_APP_API}/tanks`, tank, {
            headers
          });
          if (response.ok) {
            this.feedback.server.tank = `Tank ${this.tank_name} succesfully created.`;
            setTimeout(async () => (this.feedback.server.tank = ``), 5000);
          }
          this.$emit('update');
        } catch (err) {
          console.error(err);
        }
      } else {
        this.feedback.tank_name = `Tank ${this.tank_name} already exists.`;
      }
    }
  }
});
